.login {
    height: 100vh;
    background: #f8f8f8;
    display: grid;
    place-items: center;
}

/*   box surrounded effect is a cobination of the factor below   */
.login__container {
    padding: 97px;
    text-align: center;
    background-color: white;
    border-radius: 100px;
    box-shadow: 0 1px 12px rgba(0,0,0,12), 0 1px 2px rgba(0,0,0,24) ;
    
}

.login__container > img {
    object-fit: contain;
    height: 100px;
    margin-bottom: 40px;
}

.login__container > button {
    margin-top: 50px;
    text-transform: inherit !important;
    background-color: #0a8d48 !important;
    color: white;
}