
.sidebar {
    color:white;
    background-color: var(--slack-color);
    border-top: 1px solid #49274b;
    max-width:260px;
    flex:0.3;
}

.sidebar > hr{
    margin-top: 10px;
    margin-bottom: 10px;
    border: 1px solid #e7f7ff;
}

.sidebar_header {
    display: flex;
    border-bottom: 1px solid #e7f7ff;
    padding-bottom: 10px;
    padding:13px;
}

.sidebar_info {
    flex: 1;
}

.sidebar_info >h2 {
    font-size: 15px;
    font-weight: 900;
    margin-bottom: 5px;
}

.sidebar_info >h3{
    display: flex;
    align-items: center;
    font-size: 13px;
    font-weight: 400;
}

.sidebar_info> h3 > .MuiSvgIcon-root{
    font-size: 14px;
    margin-top: 1px;
    margin-right: 2px;
    color: green;
}

.sidebar_header > .MuiSvgIcon-root {
    padding: 8px;
    color: #54507a;
    font-size: 18px;
    background-color: white;
    border-radius: 999px;
}
